@import url("styleguide.css");

.vsc-initialized {
  overscroll-behavior: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}
.root {
  overscroll-behavior: none;
  margin: 0;
  padding: 0;
}
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background-color: #000000;
}
.home {
  overflow-x: hidden;
  overscroll-behavior: none;
  background-color: var(--black);
}
.navigation {
  align-items: center;
  display: flex;
  justify-content: space-between;
  position: relative;
  padding: 1.5rem 5vw;
  background-color: var(--black);
}

.navigation .text-wrapper {
  color: var(--neutraloffwhite);
  font-family: "Bebas Neue-Regular", Helvetica;
  font-size: 2rem;
  letter-spacing: -0.22rem;
  font-weight: 800;
  margin-top: -1px;
  position: relative;
  width: fit-content;
}

.navigation .column {
  align-items: center;
  display: flex;
  flex: 0 0 auto;
  gap: 2rem;
  justify-content: center;
  position: relative;
}

.navigation .div {
  align-items: flex-start;
  display: inline-flex;
  gap: 2rem;
  position: relative;
}

.navigation .link {
  color: var(--neutraloffwhite);
  font-family: "Bebas Neue-Regular", Helvetica;
  font-size: 1rem;
  line-height: 1.5rem;
  position: relative;
  text-decoration: none;
}

.hero-content {
  align-items: center;
  display: flex;
  flex-direction: row;
  gap: 12rem;
  padding: 2rem 5vw;
  position: relative;
  flex-wrap: nowrap;
  align-content: stretch;
  justify-content: space-around;
}
.hero-content-sub {
  align-items: baseline;
  display: flex;
  flex-direction: column;
  gap: 3rem;
  position: relative;
  width: 40vw;
  height: auto;
  max-width: 700px;
}
.hero-header {
  color: #ffffff;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 5rem;
  position: relative;
  letter-spacing: -0.22rem;
  margin-bottom: 0%;
  font-weight: 600;
  width: 30rem;
  transform: scaley(1.2);
}
.hero-content-sub .text-wrapper {
  color: #c7c7c7fd;
  font-family: "Manrope-Regular", Helvetica;
  font-size: 1.2rem;
  line-height: 1.75rem;
  position: relative;
}
.action {
  align-items: center;
  border-radius: 4px;
  color: #000000;
  display: flex;
  gap: 0.3rem;
  justify-content: center;
  letter-spacing: -0.48px;
  line-height: 24px;
  position: relative;
  text-align: center;
}

.hero-image {
  height: auto;
  width: 25rem;
  max-width: 600px;
}
.about .hero-image {
  height: auto;
  width: 90vw;
  max-width: 500px;
}
.hero-image .portrait {
  object-fit: contain;
  width: 100%;
}
.button {
  all: unset;
  align-items: center;
  background-color: var(--primary);
  border-radius: 100px;
  box-sizing: border-box;
  display: inline-flex;
  gap: 0.75rem;
  justify-content: center;
  padding: 1.3rem 1.2rem;
  cursor: pointer;
}

.button .title {
  color: var(--neutralblack);
  font-family: var(--misc-button-font-family);
  font-size: 0.875rem;
  font-style: var(--misc-button-font-style);
  font-weight: var(--misc-button-font-weight);
  letter-spacing: var(--misc-button-letter-spacing);
  line-height: var(--misc-button-line-height);
  margin-top: -2px;
  white-space: nowrap;
}

.button .circle {
  height: 2rem;
  margin-bottom: -1rem;
  margin-top: -1rem;
  overflow: visible;
  position: relative;
  width: 2rem;
  margin-right: -1rem;
}

.button .dot {
  background-color: var(--neutralblack);
  border-radius: 1.2rem;
  height: 2rem;
  position: relative;
  width: 2rem;
}

.button .img {
  height: 2rem;
  position: relative;
  width: 1.2rem;
}

.frame {
  align-items: center;
  background-color: #222222;
  border-radius: 50px;
  display: flex;
  height: 3rem;
  justify-content: center;
  position: relative;
  width: 3rem;
  cursor: pointer;
}

.about {
  background-color: var(--black);
  display: flex;
  justify-content: center;
}
.aboutme {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}
.content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 2rem;
  position: relative;
}
.about .hero-content {
  gap: 16rem;
  padding: 10rem 5vw;
  align-items: flex-start;
  display: flex;
}
.line {
  background-color: var(--neutraloffwhite);
  height: 1px;
  width: 100%;
  opacity: 0.3;
}
.contact {
  background-color: var(--black);
  gap: 3rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.form {
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
}
.label {
  color: var(--neutraloffwhite);
  font-family: "Manrope-Regular", Helvetica;
  font-size: 1rem;
  line-height: 1.5rem;
  position: relative;
}
.sendemail {
  display: flex;
  flex-direction: column;
  gap: 1.6rem;
}
.contact-content {
  padding: 12rem 5rem;
}
.workFrame {
  font-family: "Manrope-Regular", Helvetica;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 2rem;
  max-width: 900px;
  margin: 4rem auto;
  padding: 2rem;
  background: #0d1117; /* Matches dark background */
  color: #c9d1d9; /* Neutral white text */
  border: 1px seashell; /* Subtle border */
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

.work-photo {
  flex: 1; /* Take equal space */
  max-width: 300px;
  position: relative;
  border-radius: 8px;
  overflow: hidden; /* Ensure image fits inside rounded corners */
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2);
}

.portrait {
  width: 100%;
  height: auto;
  object-fit: cover; /* Ensure the image fits properly */
}

.work-content {
  flex: 2; /* Take more space */
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.work-title {
  font-size: 1.8rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

.work-description {
  font-size: 1rem;
  line-height: 1.5;
  margin-bottom: 2rem;
}

.work-info {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  font-size: 0.9rem;
}

.work-info div {
  display: flex;
  justify-content: space-between;
  border-top: 1px solid #30363d;
  padding: 0.5rem 0;
}

.work-links {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
}

.work-links a {
  text-decoration: none;
  font-size: 1rem;
  color: #d3e879;
  padding: 0.5rem;
  align-self: center;
  transition: background 0.3s ease, color 0.3s ease;
}

.work-links a:hover {
  background: transparent;
  color: #d3e879;
}
.link {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-content: center;
  align-items: center;
}
.link-div {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-content: center;
  align-items: baseline;
}
.tech-tags {
  display: flex;
  gap: 8px;
}
.tech {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-content: center;
  align-items: center;
}

.tech-tag {
  background-color: #333; /* Koyu renk arka plan */
  color: #fff; /* Yazı rengi */
  padding: 4px 8px; /* İç boşluk */
  border-radius: 4px; /* Köşeleri yuvarlat */
  font-size: 14px; /* Yazı boyutu */
}
.hero {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* 
   Sadece mobilde (max-width: 500px) görünümü değiştiriyoruz. 
   PC versiyonu yukarıdaki gibi kalıyor.
*/
@media (max-width: 500px) {
  .navigation {
    flex-direction: column;
    padding: 1rem;
    gap: 1rem;
  }

  .navigation .text-wrapper {
    font-size: 1.5rem;
    line-height: 2rem;
    letter-spacing: 0; /* Negatif değeri azaltalım mobilde */
  }

  .hero-content {
    flex-direction: column;
    text-align: center;
    margin-bottom: 20px;
    gap: 2rem;
    padding: 1rem;
  }

  .hero-header {
    font-size: 3rem;
    line-height: 3.5rem;
    width: 100%;
    letter-spacing: 0;
    transform: none;
    margin: 0 auto;
    white-space: normal;
  }

  .hero-content .text-wrapper {
    font-size: 1rem;
    line-height: 1.4rem;
    white-space: normal;
    overflow-wrap: break-word;
  }

  .hero-image {
    margin-bottom: 20px;
    width: 80vw;
    max-width: none;
  }

  .about .hero-content {
    flex-direction: column;
    gap: 2rem;
    padding: 4rem 5vw;
    text-align: center;
  }

  .workFrame {
    flex-direction: column;
    align-items: flex-start;
    padding: 1rem;
    gap: 1rem;
    margin: 2rem auto;
  }

  .work-photo {
    max-width: 100%;
    width: 100%;
  }

  .work-title {
    font-size: 1.4rem;
  }

  .work-description {
    font-size: 0.9rem;
    margin-bottom: 1rem;
  }

  .content {
    max-width: 95vw;
    padding: 1rem 0;
  }

  .aboutme {
    gap: 1rem;
    align-items: center;
    text-align: center;
  }
}
